import './PostForm.css';
import { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import AuthService from '../../services/AuthService';
import PostService from '../../services/PostService';
import Chip from '../Utils/Chip';
import Input from '../Utils/Input';
import Select from '../Utils/Select';
import Loading from '../PlaceHolders/Loading';
const PostForm = () => {
	const history = useHistory();
	const post =
		history.location.pathname === '/addProject'
			? null
			: JSON.parse(window.sessionStorage.getItem('post'));
	const [isLogin, setIsLogin] = useState(false);
	const [header, setHeader] = useState(post ? post.header : '');
	const [brief, setBrief] = useState(post ? post.brief : '');
	const [description, setDescription] = useState(post ? post.description : '');
	const [tagsList, setTagsList] = useState([]);
	const [inputValue, setInputValue] = useState('');

	const [isError, setIsError] = useState();

	const handleDelete = (i) => {
		if (tagsList.length > 0) {
			const newList = tagsList.filter((item, index) => {
				return index !== i ? 1 : 0;
			});
			setTagsList(newList);
		}
	};

	const editPost = (e) => {
		e.preventDefault();
		if (header && brief && description && tagsList.length) {
			PostService.UpdatePost(post._id, header, brief, description, tagsList).then((results) => {
				if (results) {
					window.location.reload();
				}
			});
			return history.push('/');
		}
		setIsError(true);
	};

	const addPost = (e) => {
		e.preventDefault();
		if (header && brief && description && tagsList.length) {
			PostService.AddPost(header, brief, description, tagsList).then((results) => {
				if (results) {
					window.location.reload();
				}
			});
			return history.push('/');
		}
		setIsError(true);
	};

	const addTag = () => {
		if (tagsList.includes(inputValue)) return;
		setTagsList([...tagsList, inputValue]);
	};

	useEffect(() => {
		AuthService.Profile().then(async (user) => {
			if (user) setIsLogin(user.isLogin);
		});
	}, [tagsList]);

	const [tags, setTags] = useState([]);
	useEffect(() => {
		PostService.getTags().then((results) => {
			setTags(results);
		});
	}, []);

	return (
		<div className='postForm'>
			{isLogin ? (
				<form
					className='card'
					onSubmit={history.location.pathname === '/addProject' ? addPost : editPost}
					style={{ width: '100%', maxWidth: '600px', height: 'fit-content' }}>
					<label>Header</label>
					<Input
						required
						getInput={(e) => setHeader(e)}
						value={header}
						width={'full'}
						height={30}
						fontSize={'20px'}
						Autocomplete={true}
						isError={isError}
						error={'You must fill all the sections'}
						maxLetters={60}
					/>
					<label>Short Brief</label>
					<Input
						type='textarea'
						required
						getInput={(e) => setBrief(e)}
						value={brief}
						width={'full'}
						height={100}
						isError={isError}
						error={'You must fill all the sections'}
					/>

					<label>Description</label>
					<Input
						type='textarea'
						required
						getInput={(e) => setDescription(e)}
						value={description}
						width={'full'}
						height={200}
						isError={isError}
						error={'You must fill all the sections'}
					/>
					<div className='tagsContainer'>
						<div className='selectDiv'>
							{tags.length > 0 ? (
								<Select
									label={'Tag'}
									options={tags}
									getChosenOption={(e) => {
										setInputValue(e);
									}}
									isError={inputValue ? false : isError}
								/>
							) : (
								<Loading />
							)}

							<button
								type='button'
								style={{
									marginLeft: '5px',
									position: 'relative',
									top: '5px',
								}}
								onClick={addTag}>
								Add Tag
							</button>
						</div>
						<div className='tagList'>
							{tagsList.map((tag, index) => {
								return (
									<Chip
										label={tag}
										deleteChip={() => {
											handleDelete(index);
										}}
										key={index}
									/>
								);
							})}
						</div>
						<button
							style={{
								alignSelf: 'center',
								margin: '5px',
							}}>
							Submit
						</button>
					</div>
				</form>
			) : (
				<p style={{ color: 'var(--primary-bg-color)' }}>Need to be login to add new project</p>
			)}
		</div>
	);
};

export default PostForm;
